          <el-input type="text" show-word-limit maxlength="140" size="medium"
                    style="display: inline-block;width: 98%;margin: 15px 15px;">
          </el-input>
          <el-button size="mini" style="float:right;margin-right: 14px;"></el-button>
        </div>


        <h4 style="font-weight: 300" v-if="queryInfo.offset === 0 && mvHotComment.length!==0"></h4>
        <!---->
        <div v-for="item in mvHotComment"
             style="position: relative;border-top: 2px solid rgb(240,240,242);
                 border-bottom: 1px solid rgb(240,240,242);padding: 15px 0;font-size: 15px;">

          <div>
            <!---->
            <img :src="item.user.avatarUrl" alt=""
                 style="display: inline;width: 50px;height: 50px;border-radius: 25px;">

            <!---->
            <div style="margin-left: 60px;margin-top: -60px;">
              <p><span style="color: #4d99de;">{{ item.user.nickname }}</span>: {{ item.content }}</p>

              <br v-if="item.beReplied.length > 0">
              <!---->
              <div v-if="item.beReplied.length > 0"
                   style="background-color: rgb(241,241,244);padding: 5px 5px;">
                <p v-for="citem in item.beReplied"><span
                    style="color: #4d99de;">@{{ citem.user.nickname }}</span>: {{ citem.content }}</p>
              </div>

              <p style="display:inline;font-size: 13px;color: gray;opacity: 0.7">{{
                  item.time |
                      dateFormat
                }}</p>
              <p style="display: inline;margin-left: 500px">
                <ion-icon name="thumbs-up-outline" style="color: skyblue"></ion-icon>
                {{item.likedCount}}</p>

            </div>
          </div>
        </div>

        <!---->
        <h4 style="font-weight: 300;margin-top: 25px;">({{ commentTotal }})</h4>
        <div v-for="item in mvCommentList"
             style="position: relative;border-top: 2px solid rgb(240,240,242);
                 border-bottom: 1px solid rgb(240,240,242);padding: 15px 0;font-size: 15px;">

          <div>
            <!---->
            <img :src="item.user.avatarUrl" alt=""
                 style="display: inline;width: 50px;height: 50px;border-radius: 25px;">

            <!---->
            <div style="margin-left: 60px;margin-top: -60px;">
              <p><span style="color: #4d99de;">{{ item.user.nickname }}</span>: {{ item.content }}</p>

              <br v-if="item.beReplied.length > 0">
              <!---->
              <div v-if="item.beReplied.length > 0"
                   style="background-color: rgb(241,241,244);padding: 5px 5px;">
                <p v-for="citem in item.beReplied"><span
                    style="color: #4d99de;">{{ citem.user.nickname }}</span>: {{ citem.content }}</p>
              </div>

              <p style="display:inline;font-size: 13px;color: gray;opacity: 0.7">{{
                  item.time |
                      dateFormat
                }}</p>
              <p style="display: inline;margin-left: 500px">
                <ion-icon name="thumbs-up-outline" style="color: skyblue"></ion-icon>
                {{item.likedCount}}</p>
            </div>
          </div>
        </div>

        <!---->
        <el-pagination
            background style="margin-top: 20px"
            layout="prev, pager, next" :page-size="queryInfo.limit"
            :total="commentTotal" @current-change="handleCurrentChange">
        </el-pagination>
      </el-row>
    </el-col>

    <!--mv-->
    <el-col :span="7" :offset="1">

      <el-row style="border-bottom: 1px solid rgb(230,230,230);padding-bottom: 10px;">
        <span style="margin-left: 10px;font-size: 20px;font-weight: 400">MV</span>
      </el-row>

      <!-- -->
      <el-row style="margin-top: 10px;margin-left: 12px;">
                <span style="font-size: 14px;">:
                    <span style="font-size: 14px;">{{ mvDetailInfo.data.publishTime }}</span>
                </span>

        <span style="font-size: 14px;margin-left: 15px;float: right">:
                    <span style="font-size: 14px;">{{ (mvDetailInfo.data.playCount / 10000).toFixed(0) + '' }}</span>
                </span>
      </el-row>

      <!---->
      <el-row style="margin-top: 10px;margin-left: 12px;">
        <p style="font-size: 14px;font-weight: 400;">: {{ mvDetailInfo.data.desc }}</p>
      </el-row>

      <!---->
      <el-row style="margin-top: 45px;border-bottom: 1px solid rgb(230,230,230);padding-bottom: 10px;">
        <span style="margin-left: 10px;font-size: 20px;font-weight: 400"></span>
      </el-row>

      <!---->
      <el-row v-for="item in simiMvList" style="margin-top: 25px;">
        <el-col :span="8">
          <el-image :src="item.cover" @click="toMvPage(item.id)"
                    style="width: 200px;height: 120px;cursor: pointer;border-radius: 10px"></el-image>
          <!---->
          <div style="background: rgba(128,128,128,0.2);color: white;user-select: none;
                                    width: 100px;position:absolute;z-index: 10;top: 2px;left: 95px;">
            <div style="float:right;">
              <i class="el-icon-headset" style="margin-right: 5px;"></i>
              {{ (item.playCount / 10000).toFixed(0) }}
            </div>
          </div>
          <p style="font-size: 14px;user-select: none;color: white;
                  width: 100px;position:absolute;z-index: 10;top: 88px;left: 160px;">
            {{ item.duration / 1000 | timeFormat }}</p>
        </el-col>

        <el-col :span="11" :offset="5">
          <el-tag size="mini" type="danger" style="background-color: white;border: 1px solid red">MV</el-tag>
          <span style="font-size: 15px;margin-left: 10px;cursor: pointer"
                @click="toMvPage(item.id)">{{ item.name }}</span>

          <p style="font-size: 14px;opacity: 0.7;cursor: pointer" @click="toSearchPage(item.artistName)">by {{ item.artistName }}</p>
        </el-col>

      </el-row>

    </el-col>
  </el-row>
</template>

<script>
export default {
  data() {
    return {
      //mvid
      curMvId: this.$route.params.id,
      //mv
      mvDetailInfo: {
        data: {
          name: ''
        }
      },
      //mvurl
      mvUrl: '',
      //
      queryInfo: {
        id: this.$route.params.id,
        limit: 10,
        offset: 0,
      },
      //mv
      mvCommentList: [],
      //mv
      commentTotal: 0,
      //mv
      mvHotComment: [],
      //mvmv
      simiMvList: []
    }
  }, created() {
    //mv
    this.getMvDetailInfo()
    //mvurl
    this.getMvUrl()
    //mv
    this.getMvCommentList()
    //mv
    this.getSimiMvs()
  },
  methods: {
    //
    toSearchPage(data) {
      if (data.trim() !== '') {//
        this.$router.push('/searchBySong/' + data);
      }
    },
    //mv
    getMvDetailInfo() {
      this.$http.get('/mv/detail', {params: {mvid: this.curMvId}}).then(res => {
        this.mvDetailInfo = res.data
      })
    },
    //mvurl
    getMvUrl() {
      this.$http.get('mv/url', {params: {id: this.curMvId}}).then(res => {
        this.mvUrl = res.data.data.url
      })
    },
    //mv
    getMvCommentList() {
      this.$http.get('comment/mv', {params: this.queryInfo}).then(res => {
        this.mvCommentList = res.data.comments
        this.commentTotal = res.data.total
        this.mvHotComment = res.data.hotComments
      })
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.offset = (newPage - 1) * this.queryInfo.limit
      this.getMvCommentList();
    },
    //mv
    getSimiMvs() {
      this.$http.get('simi/mv', {params: {mvid: this.curMvId}}).then(res => {
        this.simiMvList = res.data.mvs
      })
    },
    //mv
    toMvPage(mvId) {
      this.$router.push('/toVideoPage/' + mvId);
      document.location.reload();
    }
  }
}
</script>

