<template>
  <!---->
  <el-table :data="currentMusicListInfo"
            ref="playTable"
            highlight-current-row border stripe
            style="cursor: context-menu;width: 90%;margin-left: 20px"
            empty-text="..."
            @row-dblclick="playMusicList">

    <el-table-column label="" type="index" align="center" width="50px"></el-table-column>

    <el-table-column label="" width="80px" align="center">
      <template scope="scope">
        <ion-icon name="heart-outline"></ion-icon>
        <ion-icon name="download-outline"></ion-icon>
      </template>
    </el-table-column>

    <el-table-column label="" prop="name" width="300px" :sortable="true">
      <template scope="scope">
      <span :title="scope.row.name">
          {{ scope.row.name }}
      </span>
      </template>
    </el-table-column>

    <el-table-column label="" prop="ar[0].name" width="195px;" :sortable="true">
      <template scope="scope">
      <span class="singer" v-for="(item,index) in scope.row.ar" style="cursor: pointer" @click="toSingerPage(item.id)"
            :title="index !== scope.row.ar.length - 1 ? item.name : item.name">
          {{ index !== scope.row.ar.length - 1 ? item.name + ' /' : item.name }}
      </span>
      </template>
    </el-table-column>

    <el-table-column label="" prop="al.name" width="400px" :sortable="true">
      <template scope="scope">
      <span class="alName" :title="scope.row.al.name" style="cursor: pointer">
          {{ scope.row.al.name }}
      </span>
      </template>
    </el-table-column>

    <el-table-column label="" prop="dt" width="95px;" :sortable="true"></el-table-column>

  </el-table>
</template>

<script>

export default {
  data() {
    return {
      //
      queryIds: this.$route.params.ids ? this.$route.params.ids : window.localStorage.getItem('queryIds'),
      //name url 
      currentMusicListInfo: [],
      //id
      songListInfo: [],
      //id
      curId: parseInt(window.localStorage.getItem('curPlayMusicId')),
    }
  },
  created() {
    //
    this.handlerSongList()
    // main.js
    window.addEventListener('setItem', () => {
      this.curId = parseInt(localStorage.getItem('curPlayMusicId'))
    })
  },
  mounted() {
    //
    window.localStorage.setItem('queryIds', this.queryIds)
  },
  methods: {
    //(id)
    handlerSongList() {
      this.$http.get('song/detail', {params: {ids: this.queryIds}}).then(res => {
        this.currentMusicListInfo = res.data.songs
        //
        this.currentMusicListInfo.forEach(item => {
          const dt = new Date(item.dt)
          const mm = (dt.getMinutes() + '').padStart(2, '0')
          const ss = (dt.getSeconds() + '').padStart(2, '0')

          item.dt = mm + ':' + ss
        })
        //id
        this.setSongListInfo()
      })
    },
    //,()
    playMusicList(row) {
      this.$emit('setSongListInfo', this.songListInfo, row.id)
    },
    //id
    setSongListInfo() {
      this.currentMusicListInfo.forEach(item => {
        this.songListInfo.push(item.id)
      })
    },
    //
    toSingerPage(id) {
      this.$router.push('/singer/' + id)
    }
  }
}
</script>

<style lang="less" scoped>
.singer:hover, .alName:hover {
  color: rgb(148, 148, 148);
}

ion-icon {
  padding-left: 8px;
  font-size: 20px;
}

ion-icon:hover {
  color: rgb(148, 148, 148);
}

/deep/ .el-table_1_column_4 .cell {
  cursor: pointer !important;
}
</style>
